.catalog-main {
  max-width: 1210px;
  display: flex;
  margin: 0 auto;
  padding-top: 80px;
  padding-bottom: 0;
  padding-left: 20px;
  padding-right: 20px;

  @media (max-width: $desktop-width) {
    max-width: 718px;
    padding-top: 46px;
  }

  @media (max-width: $tablet-width) {
    max-width: 330px;
    padding-top: 35px;
  }
}

.catalog-main__wrapper {
  display: flex;
  flex-wrap: wrap;
  max-width: 870px;
  justify-content: space-between;

  @media (max-width: $desktop-width) {
    max-width: 678px;
  }

  @media (max-width: $tablet-width) {
    max-width: 290px;
  }
}

.catalog-button {
  max-width: 678px;
  width: 100%;
  background-color: $brown;
  color: $white;
  font-size: 16px;
  line-height: 21px;
  padding-top: 28px;
  padding-bottom: 28px;
  margin: 0 auto;
  margin-top: 45px;
  border: none;
  text-align: center;
  display: none;

  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.5;
  }

  @media (max-width: $desktop-width) {
    display: block;
  }

  @media (max-width: $tablet-width) {
    margin-top: 36px;
    margin-bottom: 0;
    max-width: 290px;
  }
}
